/* mixins css */
/*------------------------------------------------------------------------------------------------------------ */
/* clearfix for floated elemments */
@mixin clearfix {
  &::after {
    content: "";
    display: table;
    clear: both;
  }
}

/* */
/* Absolute property names as 'top', 'bottom', 'left', 'right' */
/*  absCenter */
@mixin absCenter {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

/* absTopLeft */
@mixin absTopLeft {
  position: absolute;
  top: 0;
  left: 0;
}

/* absTopRight */
@mixin absTopRight {
  position: absolute;
  top: 0;
  right: 0;
}

/* absTopL */
@mixin absTopLI($number) {
  position: absolute;
  top: 0;
  left: $number;
}

/* absTopR */
@mixin absTopRI($number) {
  position: absolute;
  top: 0;
  right: $number;
}

/* absLeftT */
@mixin absLeftTI($number) {
  position: absolute;
  left: 0;
  top: $number;
}

/* absRightT */
@mixin absRightTI($number) {
  position: absolute;
  right: 0;
  top: $number;
}

/* absLeftT */
@mixin absTLI($top, $left) {
  position: absolute;
  left: $left;
  top: $top;
}

/* absLeftT */
@mixin absTRI($top, $right) {
  position: absolute;
  right: $right;
  top: $top;
}

/* absBottomLeft */
@mixin absBottomLeft {
  position: absolute;
  bottom: 0;
  left: 0;
}

/* absBottomRight */
@mixin absBottomRight {
  position: absolute;
  bottom: 0;
  right: 0;
}

/* absBottomL */
@mixin absBottomLI($number) {
  position: absolute;
  bottom: 0;
  left: $number;
}

/* absBottomR */
@mixin absBottomRI($number) {
  position: absolute;
  bottom: 0;
  right: $number;
}

/* absLeftB */
@mixin absLeftBI($number) {
  position: absolute;
  left: 0;
  bottom: $number;
}

/* absRightB */
@mixin absRightBI($number) {
  position: absolute;
  right: 0;
  bottom: $number;
}

/* absLeftB */
@mixin absBLI($bottom, $left) {
  position: absolute;
  left: $left;
  bottom: $bottom;
}

/* absRightB */
@mixin absBRI($bottom, $right) {
  position: absolute;
  right: $right;
  bottom: $bottom;
}

/* Flex property names as 'display', 'justify', 'align', 'direction' */
/* flexCenter */
@mixin flexCenter($direction) {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: $direction;
}

