/* WEBKIT SCROLLBAR*/
@mixin WebkitScrollbar() {
  overflow: hidden !important;

  &:hover {
    overflow: auto !important;

    @supports (overflow: overlay) {
      overflow: overlay !important;
    }
  }

  &::-webkit-scrollbar {
    width: 4px;
    height: 2px;
  }

  &::-webkit-scrollbar-track {
    background: $transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--webkit-scrollbar);
    border-radius: $radius;
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    -o-border-radius: $radius;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: var(--webkit-scrollbar);
  }
}

@mixin WebkitScrollbarY() {
  overflow-x: hidden;
  overflow-y: hidden;

  &:hover {
    overflow-y: auto;

    @supports (overflow-y: overlay) {
      overflow-y: overlay;
    }
  }

  &::-webkit-scrollbar {
    width: 4px;
    height: 2px;
  }

  &::-webkit-scrollbar-track {
    background: $transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--webkit-scrollbar);
    border-radius: $radius;
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    -o-border-radius: $radius;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: var(--webkit-scrollbar);
  }
}

@mixin WebkitScrollbarX() {
  overflow-x: hidden !important;
  overflow-y: hidden !important;

  &:hover {
    overflow-x: auto !important;

    @supports (overflow-x: overlay) {
      overflow-x: overlay !important;
    }
  }

  &::-webkit-scrollbar {
    width: 4px;
    height: 2px;
  }

  &::-webkit-scrollbar-track {
    background: $transparent;
  }

  &::-webkit-scrollbar-thumb {
    background: var(--webkit-scrollbar);
    border-radius: $radius;
    -webkit-border-radius: $radius;
    -moz-border-radius: $radius;
    -ms-border-radius: $radius;
    -o-border-radius: $radius;
  }

  &::-webkit-scrollbar-thumb:hover {
    background: var(--webkit-scrollbar);
  }
}

@mixin WebkitScrollYFixedWidth {
  scrollbar-width: thin;
  scrollbar-color: transparent transparent;

  &:hover {
    scrollbar-color: var(--webkit-scrollbar) transparent;
  }
  &::-webkit-scrollbar {
    width: 4px;
  }
  &::-webkit-scrollbar-thumb {
    background-color: transparent;
  }
  &:hover::-webkit-scrollbar-thumb {
    background-color: var(--webkit-scrollbar);
    border-radius: 10px;
  }
  &::-webkit-scrollbar-track {
    background: transparent;
  }
}

