        z-index: 99;
        padding: calc($space * 3);

        .logodiv {
          width: 100%;
          height: 100%;
          color: $primary-light;
font-family: 'Roboto', sans-serif;          display: flex;
          flex-direction: column;
          justify-content: space-around;
          .logo {
            width: 40px;
            height: 60px;
            svg {
              height: 100%;
              width: 100%;
              color: $secondary-light;
              object-fit: contain;
            }
          }
          .starting_page {
            span {
              font-size: $fontsize-large7;
              color: $secondary-light;
              line-height: 150%;
font-family: 'Roboto', sans-serif;            }
          }
        }
      }

      .bottom {
        @include flexCenter(column);
        flex: 1;
        width: 100%;
        height: 60%;

        .watchlist {
          height: 100%;
          width: 100%;

          .carousel {
            height: 100%;
            width: 100%;

            .slider {
              width: 100%;
              height: 334px;
              .container_top {
                height: 10px;
                padding: 0 calc($space * 3);
              }
              .slide {
                width: 20px;
                display: flex;
                align-items: center;
                flex-direction: column;

                .profile {
                  display: flex;
                  align-items: center;
                  gap: 1rem;
                  height: 80px;
                  color: $primary-light;
                  .profile_data {
                    @include flexColumnStartCenter();
                    .tagline {
                      font-size: $fontsize-small2;
                    }
                  }
                }

                p {
                  margin: 0;
                  font-weight: 400 !important;
                  font-size: $fontsize-large1 !important;
                }

                .twitter-icon {
                  margin-top: $space;
                  color: $primary-light;
                  height: 20px;
                  width: 20px;
                }

                .ImageRes-container {
                  width: 40px;
                  height: 40px;
                  max-height: calc(100% - 200px);
                  margin-bottom: calc($space * 4.8);
                }

                .ImageRes-container-img {
                  width: 40px;
                  height: 40px;
                  object-fit: contain;
                  // border: 1px solid red;
                  border-radius: 70% !important;
                  -webkit-border-radius: 70% !important;
                  -moz-border-radius: 70% !important;
                  -ms-border-radius: 70% !important;
                  -o-border-radius: 70% !important;
                }

                p {
                  font-weight: 600;
                  font-size: $fontsize-large6;
                  line-height: 150%;
                  color: $primary-light;
                }
              }
            }
          }
        }
      }
    }

