        padding-left: calc($space * 0.8);
        padding-bottom: calc($space * 1.6);
        display: none;
        border-bottom: 1px solid var(--border-color);

        @include res(tab-lg) {
          display: none;
        }

        .ImageRes-container {
          width: fit-content;
        }
      }

      .error-box {
        margin-top: calc($space / 1.25);
        padding: calc($space / 1.25);
        border: 1px solid var(--primary-red);
        background-color: var(--tag-bg-red);
        color: var(--text-primary);
        border-radius: $radius;
        font-weight: 400;
        font-size: $fontsize-default;
        line-height: 130%;
      }

      ._signup,
      ._login,
      ._forgot,
      ._mobileverification,
      ._getotp,
