          /* Terms and Condition */
          .terms_condition {
            @include flexColumnStart();
            margin-top: calc($space / 1.25);
            width: 100%;
            gap: calc($space / 1.25);
            height: 48px;

            .update_whatsapp,
            .terms {
              @include flexRowStartCenter();
              width: 100%;
              height: 20px;

              p {
                font-size: $fontsize-default;
                line-height: 130%;
                font-weight: 400;
              }

              input[type="checkbox"] {
                display: none;
                /* hide the default checkbox */
              }

              /* unchecked state */
              label {
                margin: calc($space / 1.25) calc($space / 1.25)
                  calc($space / 1.25) calc($space * 0);
                display: inline-block;
                width: 16px;
                height: 16px;
                padding: calc($space * 0);
                border: 2px solid $blue-light-200;
                border-radius: 4px;
              }

              /* checked state */
              input[type="checkbox"]:checked + label {
                border-color: $blue-light-200;
                background-color: $blue-light-200;
                color: $secondary-light;
              }

              /* checkmark */
              label::before {
                content: "";
                display: block;
                text-align: center;
                line-height: 12px;
                font-size: $fontsize-small2;
              }

              input[type="checkbox"]:checked + label::before {
                content: "\2714";
                font-size: $fontsize-small2;
              }
            }
          }

