            }

            h4 {
              margin-bottom: calc($space / 2.5);
              font-weight: 600;
              font-size: $fontsize-large8;
              line-height: 150%;
              text-align: center;
              color: $text-dark-primary !important;

              @include res(tab-lg) {
                font-size: $fontsize-large4;
              }
            }

            span {
              font-weight: 400;
              font-size: $fontsize-large1;
              line-height: 140%;
              text-align: center;
              color: $text-dark-secondary !important;

              @include res(tab-lg) {
                font-size: $fontsize-default;
              }
            }
          }

          .ghostbutton {
            width: 90%;
            height: 2rem;
            text-align: center;
            background-color: $blue-light-200 !important;
            color: $secondary-light !important;
            cursor: pointer;
            border-radius: 4px;
            font-size: $fontsize-default;
            font-weight: 500;
            margin-top: calc($space * 3.2);

            @include res(tab-lg) {
              margin-top: calc($space * 10);
            }
          }
        }
      }
    }
  }
}
