.input-set {
  @include flexColumnStart();
  gap: calc($space * 0.4);
  width: 100%;

  // &-disabled {
  //   background-color: $modal-ghost-border !important;
  //   opacity: 1;
  // }

  label {
    // color: $text-dark-muted;
    font-size: $fontsize-default;
    font-weight: 600 !important;
    line-height: 120%;
  }

  .rs-btn-xs {
    padding: 0;
    line-height: $space;
  }

  .rs-btn-link {
    text-decoration: none;
    color: var(--webinar-modal-copy-link);
    font-size: $fontsize-small2;
    font-weight: 500;
    padding-right: calc($space);
  }

  .label-line {
    width: 100%;
    @include flexRowBetweenCenter();
    height: fit-content;
    line-height: $space;
  }

  .label-line2 {
    width: 100%;
    color: $text-dark-muted;
    @include flexRowBetweenCenter();
    height: fit-content;
    line-height: $space;
    label {
      font-size: $fontsize-small2 !important;
    }
  }

  .req-star {
    color: var(--primary-red);
    font-size: calc($fontsize-small2 * 0.6);
    margin-bottom: 2px;
  }

  .error {
    font-weight: 400;
    font-size: $fontsize-default;
    color: var(--primary-red);
  }

 

